.card {
  border-top: 1px dashed #e0e0e0;
  padding: 1rem;
  display: inline-block;
}

.chip {
  display: inline-block;
  margin: 0 0.5rem 0.5rem 0;
  font-size: 0.7rem;
  font-weight: bold;
}

.link--title {
  text-decoration: none;
  color: black;
}

@media only screen and (min-width: 992px) {
  .card {
    width: 40%;
    min-height: 100px;
  }
}